@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
* {
  box-sizing: border-box;
}

img {
  max-width: 100%
}

html {
  display: grid;
  min-height: 100%;
}

body {
  font-family: 'Roboto', sans-serif;
  padding: 0;
  margin: 0;
  display: grid;
  background: linear-gradient(to bottom left, #79C7C5 40%, #F9FBFF 100%);
  min-height: 100%;
  font-size: 14px;
}

img {
  color: #fff;
}

a {
  /* text-decoration: none; */
  color: #fff;
}

#root {
  height: 100vh;
}

.loginContainer {
  display: grid;
  place-items: center;
  height: 100vh;
}

.loginInnerContaier {
  min-width: 20%;
  width: 300px;
  margin: 0 auto;
}

.loginInput {
  border-radius: 0;
  padding: 15px 20px;
  width: 100%;
  margin-top: 15px;
}

.heading {
  text-align: center;
  font-size: 34px;
  border-bottom: 1px solid rgba(255,255,255,.6);
  padding-bottom: 15px;
  font-weight: 400;
  margin-top: 0;
}

.button {
  color: #fff;
  text-transform: uppercase;
  text-decoration: none;
  background: #79C7C5;
  padding: 4px 10px;
  border-radius: 4px;
  display: inline-block;
  border: none;
  width: 100%;
  margin-top: 20px;
  font: 14px 'Roboto', sans-serif;
  height: 42px;
  cursor: pointer;
}

@media (min-width: 320px) and (max-width: 480px) {
  .loginContainer {
    height: 100%;
  }

  .loginInnerContainer {
    width: 90%;
  }

}

button:focus {
  outline: 0;
}
.chatWrapper {
  position: relative;
  display: grid;
  height: 100%;
  min-height: 600px;
  align-items: center;
  justify-content: center;
  padding: 10px;

}

.chatHeading {
  text-align: center;
  grid-column: 1/3;
  margin: 0;
  padding: 0 0 10px;
  font-size: 30px;
  max-width: 700px;
  font-weight: 400;
}

.chatMembersTitle {
  margin: 0;
  font-size: 20px;
  font-weight: 400;
  padding: 0 0 10px;
}

.chatContainer {
  box-shadow: 5px 5px 15px rgba(#777777, 0.5);
  display: grid;
  grid-template-columns: 150px 1fr;
  width: 800px;
  max-width: 100%;
  grid-gap: 12px;
}

.chatMembers {
  padding-left: 10px;
  font-size: 22px;
  font-weight: 700;
  min-width: 15%;
  padding-left: 0;
  margin: 0;
}

.chatMembersList {
  list-style: none;
  margin: 0;
  padding: 0;
  max-height: 300px;
  overflow: auto;
  font-size: 12px;
}

.chatSingleMember{
  display: grid;
  align-items: center;
  width: 100%;
  grid-template-columns: 26px 1fr 20px;
  grid-gap: 8px;
  text-align: left;
  padding: 5px;
  border-radius: 5px;
  margin-bottom: 5px;
}

.chatSingleMember.active{
  background: rgba(255,255,255,0.4);
}

.chatInnerContainer {

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: #deeceb;
  border-radius: 8px;
  height: auto;
  width: 100%;
  height: 450px;
}

.inputFile {
  display: none;
}




.formMessages {
  display: flex;
  background-color: #fff;
  border-radius: 0 0 8px 8px;
  overflow: hidden;
  height: 55px;
  flex-shrink: 0;
}

.formMessages input[type=text] {
  height: 100%;
  border-radius: 0 0 0 8px;
}

.inputMessage {
  border: none;
  padding: 30px;
  width: 80%;
  font-size: 1.2em;
}

input[type=text] {
  display: block;
  width: 100%;
  height: 42px;
  background: #fff;
  padding: 4px 12px;
  font: 400 14px/1.5 'Roboto', sans-serif;
  color: #495057;
  background-clip: padding-box;
  border: 1px solid #fff;
  border-radius: 4px;
  transition: all .4s ease-in;
}

input:focus, textarea:focus, select:focus{
  outline: none;
  border-color: #79C7C5;
}

.sendMessageBtn {
  text-transform: uppercase;
  text-decoration: none;
  background: rgba(0,0,0,.1);
  display: inline-block;
  border: none;
  width: 100px;
}

.emojiBtn{
  background: none;
  display: inline-block;
  border: none;
  width: 50px;
  min-width: 10px;
  font-size: 25px;
  margin-right: 7px;
}

.sentFilesBtn {
  text-transform: uppercase;
  text-decoration: none;
  background: none;
  padding: 10px;
  display: inline-block;
  border: none;
  width: 44px;
  min-width: 10px;
  background-size: 70%;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url(data:image/png;base64,);
}

.emoji {
  position: absolute;
}

::-webkit-input-placeholder { /* Chrome/Opera/Safari */
  color: #6c757d;
}
::-moz-placeholder { /* Firefox 19+ */
  color: #6c757d;
}
:-ms-input-placeholder { /* IE 10+ */
  color: #6c757d;
}
:-moz-placeholder { /* Firefox 18- */
  color: #6c757d;
}
.messageContainer {
  justify-content: flex-start;
  margin-top: 3px;
  padding: 5px 10px 0;
}

.messageSentBy {
  background: #79C7C5;
  border-radius: 20px;
  padding: 9px 14px;
  color: white;
  display: inline-block;
  max-width: 80%;
}

.messageSentBy.colored {
  background-color: #989898;
}

.sentMessage {
  padding: 7px 11px 4px;
  margin: 0;
  font-size: 12px;
}

.reverse {
  text-align: right;
}

.messageBox {
  width: 100%; 
  font-size: 13px;
  margin: 0;
  text-align: left;
}

.messageBox .messageImg {
  max-height: 150px;
  display: block;
  margin: 0 auto;
}

.messageBox .messageFile {
  max-height: 15px;
  display: inline-block;
  vertical-align: middle;
  /* margin: 0 auto; */
}

.messageBox a {
  vertical-align: middle;
  padding-left: 3px;
}
.messages {
  overflow: auto;
}

.messages > div {
  padding-bottom: 10px;
}





